.progress-wrap {
  height: 46px;
  width: 46px;
  cursor: pointer;
  z-index: 10000;
  opacity: 0;
  visibility: hidden;
  border-radius: 50px;
  transition: all .2s linear;
  display: block;
  position: fixed;
  bottom: 30px;
  right: 30px;
  transform: translateY(15px);
  box-shadow: inset 0 0 0 2px #0000001a;
}

.progress-wrap.active-progress {
  opacity: 1;
  visibility: visible;
  transform: translateY(0);
}

.progress-arrow {
  text-align: center;
  color: #0000004d;
  height: 30px;
  width: 30px;
  cursor: pointer;
  z-index: 2;
  transition: all .2s linear;
  display: block;
  position: absolute;
  top: 8px;
  left: 8px;
}

.progress-wrap:hover .progress-arrow {
  color: #000;
}

.progress-wrap svg path {
  fill: none;
}

.progress-wrap svg.progress-circle path {
  stroke: #0003;
  stroke-width: 4px;
  box-sizing: border-box;
  transition: all .2s linear;
}

